html {
  scroll-behavior: smooth;
}

body {
  background-color: white;
  font-family: 'Lato', sans-serif;
}

button {
  letter-spacing: 1px;
  margin: .5em;
  padding: 7px 15px;
  font-weight: 500;
  text-align: center;
  color: white;
  border: none;
  font-size: 14;
  border-radius: 0.5em;
  background-color: #4F90FF;
  cursor: pointer;
}

button:active {
  vertical-align: top;
  padding: 8px 13px 6px;
  background-color: #417BDC;
}

button:focus {
  outline: none;
}

.imageStyle {
  max-width: 95%;
  margin: 10px;
  border-radius: 0.7em;
}

.smallSpacing {
  width: 100%;
  height: 2em;
}

.mediumSpacing {
  width: 100%;
  height: 4em;
}

.largeSpacing {
  width: 100%;
  height: 14em;
}

#logo {
  float: left;
  margin: 2em 0 2em 3em;
  height: 2em;
}

.icon {
  display: inline;
  vertical-align: middle;
  width: 30px;
  padding: 5px;
}

#navbar {
  float: right;
}

#navbar ul {
  list-style: none;
  margin: 2em 3em 2em 0;
  padding: 0;
}

#navbar li {
  display: inline-block;
  margin-left: 3em;
  font-weight: 300;
  line-height: 40px;
  color: #707070;
}

.card {
  display: inline-block;
  padding: 10px 25px;
  margin: auto;
  text-align: left;
  width: 45%;
  max-width: 300px;
  min-width:190px;
  background: rgba(94, 141, 223, 0.2);
  border-radius: 0.7em;
  position: relative;
  z-index: 1;
  backdrop-filter: blur(20px);
  border: solid 2px transparent;
  background-clip: padding-box;
  box-shadow: 10px 10px 10px  rgba(46, 54, 68, 0.03);
}

.card h2 {
  margin: 10px 0;
  font-size: 1.6em;
  font-weight: 700;
  color: #505050;
  text-align: center;
}

.card p {
  font-size: 1em;
  font-weight: 500;
  color: #505050;
}

a{
  text-decoration: none;
  color: #707070;
}


a:hover {
  text-decoration: none;
  color: #000000;
}

